import React, { useState } from "react";
import { AddContact } from '../models/ContactModel';
import InputType from '../Components/InputType';
import { isValidForm } from '../Utility/ValidationCheck';

const Contact = () =>{

  const [formData, setformData] = useState({
    firstName: '',
    lastName: '',
    phone: '',
    email: '',
    message: ''
  });

  const InputEvent = async(e) => {

    let {name, value} = e.target;
    value = value.toString();
    value = value? value.trim(): '';
    setformData((oldVal) => {
      return {...oldVal, [name]: value};
    });

  };

  const FormSubmit = async(e) => {

    e.preventDefault();
    let btn = document.getElementById('submitBtn');
    btn.setAttribute('disabled','disabled');
    let isValid = await isValidForm(formData);
    if(isValid){
      await AddContact(formData);
    }
    btn.removeAttribute('disabled');
    
  };

  return (

    <>
      <div className="my-5">
        <h1 className="text-center"> Contact US </h1>
      </div>
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
            <form onSubmit={FormSubmit}>

                <InputType 
                  id="FormControlInput1" 
                  labelName="Enter First Name:* " 
                  type="text" 
                  name="firstName"
                  placeholder="Enter First Name"
                  InputEvent = {InputEvent}
                  value = {formData.firstName}
                  required={true}
                />

                <InputType 
                  id="FormControlInput2" 
                  labelName="Enter Last Name*: " 
                  type="text" 
                  name="lastName"
                  placeholder="Enter Last Name "
                  InputEvent = {InputEvent}
                  value = {formData.lastName}
                />

                <InputType 
                  id="FormControlInput3" 
                  labelName="Enter Phone: " 
                  type="number" 
                  name="phone"
                  placeholder="Enter Phone"
                  InputEvent = {InputEvent}
                  value = {formData.phone}
                />

                <InputType 
                  id="FormControlInput3" 
                  labelName="Enter Email*: " 
                  type="email" 
                  name="email"
                  placeholder="Enter Email"
                  InputEvent = {InputEvent}
                  value = {formData.email}
                  required={true}
                />

                <div className="mb-3">
                    <label htmlFor="exampleFormControlTextarea5" className="form-label">
                      Message
                    </label>
                    <textarea
                      className="form-control"
                      id="exampleFormControlTextarea5"
                      rows="3"
                      name="message"
                      onChange={InputEvent}
                      value={formData.message}
                    ></textarea>
                </div>

                <div className="col-12">
                    <button className="btn btn-outline-primary" id="submitBtn" type="submit" >
                      Submit
                    </button>
                </div>
            
            </form>
          </div>
        </div>
      </div>
    </>
  
  );

};

export default Contact;
