import React, { useState, createContext } from 'react';
import InputType from '../Components/InputType';
import { useHistory} from 'react-router-dom';

const Auth = createContext();

const Login = () =>{

    const [formData, setFormData] = useState({
        email: '',
        password: ''
    });

    const history = useHistory();

    const InputEvent = (e) =>{
        let {name, value} = e.target;
        value = value.toString();
        value = value? value.trim(): '';
        setFormData((oldVal)=>({...oldVal, [name]: value}));
    };

    const FormSubmit = (e) =>{
        e.preventDefault();
        if(formData.email==='b' && formData.password==='123456'){
            localStorage.setItem('auth', formData.email);
            history.push('/dashbord');
        }
    };



    return(
        <>
      <div className="my-5">
        <h1 className="text-center"> Login </h1>
      </div>
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
            <form onSubmit={FormSubmit}>

                <InputType 
                  id="FormControlInput3" 
                  labelName="Enter Email*: " 
                  type="text" 
                  name="email"
                  placeholder="Enter Email"
                  InputEvent = {InputEvent}
                  value = {formData.email}
                  required={true}
                  autoComplete="off"
                />

                <InputType 
                  id="FormControlInput4" 
                  labelName="Enter Password*: " 
                  type="password" 
                  name="password"
                  placeholder="******"
                  InputEvent = {InputEvent}
                  value = {formData.password}
                  required={true}
                  autoComplete="off"
                />

                <div className="col-md-12">
                    <button className="btn btn-primary" id="submitBtn" type="submit" >Submit</button>
                </div>
            
            </form>
          </div>
        </div>
      </div>
    </>
    );
}

export default Login;
export { Auth};