import React from 'react';
import { required } from '../Utility/ValidationCheck';

const InputType = (props) =>{

    return(
        <>
            <div className="mb-3">

                <label htmlFor={props.id} className="form-label">
                  {props.labelName}
                </label>
                <input
                  type={props.type}
                  className="form-control"
                  id={props.id}
                  name={props.name}
                  placeholder={props.placeholder}
                  onChange={props.InputEvent}
                  value={props.value}
                  autoComplete={(props.autoComplete)? props.autoComplete: "off"}
                />
                <label htmlFor={props.id} className="form-label" style={{color: '#f00'}}>
                  {required(props.required, props.value, props.labelName)}
                </label>

            </div>
        </>
    );

};

export default InputType;