import React from 'react';
import {Redirect} from 'react-router-dom';

const ProtectedRout = (props)=>{
    const Comp = props.comp;
    const auth = localStorage.getItem('auth');
    return(
        <>
            {(auth)?<Comp />: <Redirect to='/' />}
        </>
    );

}

export default ProtectedRout;