const required = (isCheck, value, labelName) => {
    if(isCheck){
        value = value? value.toString(): '';
        value = value.trim()? '': `Please Enter Your ${labelName}`
        return value;
    }
    return '';
    
}

const isValidForm = async(obj) =>{
    try{
        let values = Object.values(obj);
        let isValid = true;
        if(values && values.length > 0){
            for(let i in values){
                if(values[i] === ''){
                    isValid = false;
                    break;
                }
            }
        }
        return isValid;
        
    }catch(e){
        throw new Error(e.toString());
    }
}

export { required, isValidForm }